const {
    SET_HOWTO_BUY,
    SET_HOWTO_BUY_LOADING

} = require('../actions/types');

const initialState = {
    howtobuy:[],
    loading:false
}
export default function(state = initialState,action){
    switch(action.type){
        case SET_HOWTO_BUY:
            return{
                ...state,
                howtobuy:action.payload.normalHowto
            }
        case SET_HOWTO_BUY_LOADING:
            return{
                ...state,
                loading:action.payload
            }   
        default:
            return state
    }
}
