import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import SocketContext from '../../socket-context'

import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import {Link, withRouter} from 'react-router-dom';

import {Col, Container, Row} from 'reactstrap'
import Button from '@material-ui/core/Button'
import FaceBookIcon from '@material-ui/icons/Facebook'
import TextField from '@material-ui/core/TextField';
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import DialogComponent from '../../components/DialogComponent';
import GoogleLogin from 'react-google-login';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import api from '../../services/ApiService.js'

const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
    },
});
class SigninComponent extends Component{
    state = {
        formData: {
            email:'',
            password: '',
        },
        submitted: false,
        disableForgotPassword:false
    };

    componentDidMount(){
      
        if ( this.props.auth.isAuthenticated ) {
			this.props.history.push( '/' );
        }
        
        ValidatorForm.addValidationRule('isPasswordMatch', (value) => {
            if (value !== this.state.formData.password) {
                return false;
            }
            return true;
        });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
          this.props.history.push("/");
        }
      }
    handleChange = (event) => {
        const { formData } = this.state;
        formData[event.target.name] = event.target.value;
        if (event.target.name === 'password') {
            this.form.isFormValid(false);
        }
        this.setState({ formData });
    }

    handleSubmit = () => {
       this.setState({
           submitted:false
       })
       this.props.loginUser(this.state.formData)
    }
    forgotPassword=()=>{
        //this.props.forgotPassword(this.state.formData.email)
        this.setState({
            disableForgotPassword:true
        })
        api.post('/auth/forgotpassword',{
            param:{
                email:this.state.formData.email
            }
        })
        .then(res=>{
            if(res.data.result){
                this.props.showDlg({
                    type:'success',
                    title:'Reset Password',
                    msg :res.data.msg
                    
                })
            }else
            {
                this.props.showDlg({
                    type:'fail',
                    title:'Reset Password',
                    msg :res.data.msg
                    
                })
            }
            this.setState({
                disableForgotPassword:false
            })
        })
    }
    googleSuccess = (e) =>{
        /*
        alert('success')
        console.log(e)
        */
       let profile = e.profileObj
       console.log(e)
       this.props.loginUserWithGoogle(profile)
    }
    googleFail = (e) =>{
        //alert('fail')
        console.log(e)
    }
    render(){
    //    const { submitted } = this.state;
        const formsubmit = (this.props.auth.regResult == 1 || this.props.auth.regResult == 3)
        return(
            <ThemeProvider theme={myTheme}>
            <Container fluid={true} style={{margin:'0px',padding:'0px'}}>

                <Row style={{height:'100vh',margin:'0px',padding:'0px'}}>

                    <Col lg="6" xs="12"  style={{display:'flex',justifyContent:'center',alignItems:'center',width:'50%'}}>
                        <div style={{margin:'10px'}}>
                            <Row>
                                <h1 style={{margin:'10px'}}>Sign In</h1>
                            </Row>
                            <Row>
                                <Col lg="8" xs="12" >
                                <p style={{opacity:'.6',margin:'10px'}}>
                                 Please enter your email and password to enter the account
                                </p>
                                </Col>
                            </Row>
                            <Row>
                                <p  style={{opacity:'.6',margin:'10px'}}>
                                    SignIn With:
                                </p>
                            </Row>
                            <Row>
                                <Col lg="5" xs="6" >
                                    <div style={{margin:'10px'}}>
                                        <Button fullWidth color="primary" startIcon={<FaceBookIcon />} variant='outlined' style={{color:'rgba(73, 154, 236, 1)',height:'50px',border:'1px solid var(--main-color)'}}>Facebook</Button>
                                    </div>
                                </Col>
                                <Col lg="5" xs="6">
                                    {/* <Button fullWidth color="primary" variant='outlined' style={{color:'rgba(73, 154, 236, 1)',height:'50px',margin:'5px'}}>Google</Button> */}
                                    <GoogleLogin
                                        clientId="240263392067-dn3bngvgf59op8h4fh5urnrsgu2c2i23.apps.googleusercontent.com"
                                        buttonText="Signin with Google"
                                        onSuccess={this.googleSuccess}
                                        onFailure={this.googleFail} 
                                        cookiePolicy={'single_host_origin'}
                                        render={renderProps => (
                                            <div onClick={renderProps.onClick} style={{margin:'10px'}}>
                                               <Button fullWidth  onClick={renderProps.onClick} disabled={renderProps.disabled}  variant='outlined' style={{color:'rgba(73, 154, 236, 1)',border:'1px solid var(--main-color)',height:'50px'}}>Google</Button>
                                            </div>
                                          )}
                                        >
                                    </GoogleLogin>
                                </Col>
                            </Row>
                            <Row>
                                <ValidatorForm
                                    ref={r => (this.form = r)}
                                    onSubmit={this.handleSubmit}
                                    style={{
                                        width:'100%',
                                        margin:'10px'
                                    }}
                                >
                                
                                    <TextValidator
                                        id="outlined-password-input"
                                        onChange={this.handleChange}
                                        name="email"

                                        label="Email"
                                        type="email"
                                        variant="outlined"
                                        validators={['required']}
                                        errorMessages={['this field is required']}

                                        fullWidth
                                        style={{margin:'5px 0px 0px 0px'}}
                                        value={this.state.formData.email}
                                    />
                                    <TextValidator
                                        label="Password"
                                        onChange={this.handleChange}
                                        name="password"
                                        type="password"
                                        variant="outlined"
                                        fullWidth
                                        style={{marginTop:'15px'}}

                                        validators={['required']}
                                        errorMessages={['this field is required']}
                                        value={this.state.formData.password}
                                        InputProps={{
                                            endAdornment:(
                                                <div style={{margin:'3px 0px 3px 7px'}}>

                                                <Button style={{textTransform:'none'}}
                                                 onClick={this.forgotPassword}
                                                 disabled={this.state.disableForgotPassword?true:this.state.formData.email == ''?true:false}
                                                color="primary">
                                                    forgot?
                                                </Button>
                                                </div>
                                            )
                                        }}
                                    />
                                    <Button
                                        color="primary"
                                        variant="contained"
                                        type="submit"
                                        fullWidth
                                        style={{color:'white',height:'50px',marginTop:'15px',marginBottom:'10px'}}
                                    >
                                        {
                                            'Sign In'
                                        }

                                    </Button>
                                    
                                
                                    <div style={{textAlign:'center',opacity:'.7'}}>
                                    Don't have an account? <Link to="/signup"><Button style={{color:'rgba(73, 154, 236, 1)'}}>Sign Up</Button></Link>
                                    </div>

                                </ValidatorForm>

                               
                            </Row>
                           
                        </div>
                    </Col>
                    <Col lg="6" xs="12" style={{margin:'0px',padding:'0px'}}>
                        <img src="/static/assets/img/car/1.jpg" style={{width:'100%',height:'100%'}}></img>
                    </Col>
                </Row>
            </Container>
            </ThemeProvider>
        )
    }
}
const SignincomponentWithSocket = (props) => (
    <SocketContext.Consumer>
        {socket => <SigninComponent {...props} socket={socket}/>}
    </SocketContext.Consumer>
)
const mapStateToProps = (state) =>({
    auth:state.auth
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
        loginUser :AuthAction.loginUser,
        //forgotPassword:AuthAction.forgotPassword,
        loginUserWithGoogle:AuthAction.loginUserWithGoogle,
        showDlg             :GlobalAction.showDialog

    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(SignincomponentWithSocket)