import {
    SET_HOWTO_BUY,
    SET_HOWTO_BUY_LOADING
 } from './types'
import api from '../../services/ApiService.js'
export const getHowtobuy = (param) => dispatch=>{
    dispatch({
        type:SET_HOWTO_BUY_LOADING,
        payload:true
    })
    api.get('/howtobuy/',{
        params:param
    })
    .then(res=>{
        dispatch({
            type:SET_HOWTO_BUY_LOADING,
            payload:false
        })
        dispatch({
            type:SET_HOWTO_BUY,
            payload:res.data
        })
    })
    
}
