import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import {Col, Container, Row} from 'reactstrap'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button'
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';

import jwt_decode from "jwt-decode";
import api from '../../services/ApiService.js'

const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
    },
  });
  
  class ResetPasswordComponent extends Component{

    state={
        disablebutton:false,
        formData:{
            password:'',
            resetpassword:''
        }
        
    }
    componentDidMount(){
      
        
        if ( this.props.auth.isAuthenticated || !this.props.match.params.token) {
			this.props.history.push( '/' );
        }
        ValidatorForm.addValidationRule('isPasswordMatch', (value) => {
            if (value !== this.state.formData.password) {
                return false;
            }
            return true;
        });
        //console.log(this.props.match.params.token)
    }
    handleChange = (event) => {
        const { formData } = this.state;
        formData[event.target.name] = event.target.value;
        if (event.target.name === 'password') {
            this.form.isFormValid(false);
        }
        this.setState({ formData });
    }
    handleSubmit = () => {
        const currentTime = Date.now() / 1000
        const decoded = jwt_decode(this.props.match.params.token);
        console.log(currentTime)
        console.log(decoded.exp)

        if(decoded.exp > currentTime)
        {
            this.setState({disablebutton:true})
            api.post('/auth/resetPassword',{
                param:{
                    token:this.props.match.params.token,
                    password:this.state.formData.password
                }
            })
            .then(res=>{
                if(res.data.result == true){
                    this.props.showDlg({
                        type:'success',
                        title:'Reset Password',
                        msg :'You have added 4 cars already. To add more please log in or create an account',
                        closeAction:()=>{
                            this.props.history.push('/signin')
                        }
                    })
                }else
                {
                    this.props.showDlg({
                        type:'fail',
                        title:'Reset Password',
                        msg :'You have added 4 cars already. To add more please log in or create an account',
                        closeAction:()=>{
                            this.props.history.push('/signup')
                        }
                    })
                }
                this.setState({disablebutton:false})

            })
        }else
        {
            alert('expired')
        }
    }
    render(){
        return(
            <ThemeProvider theme={myTheme}>

                <Container fluid={true} style={{margin:'0px',padding:'0px'}}>

                    <Row style={{height:'100vh',margin:'0px',padding:'0px'}}>
                        <Col lg="6" xs="12"  style={{display:'flex',justifyContent:'center',alignItems:'center'}}>
                        <div style={{}}>

                        <ValidatorForm
                                    ref={r => (this.form = r)}
                                    onSubmit={this.handleSubmit}
                                    style={{
                                        width:'100%',
                                        margin:'10px'
                                    }}
                                >
                                
                                    <TextValidator
                                        id="outlined-password-input"
                                        onChange={this.handleChange}
                                        name="password"

                                        label="Password"
                                        type="password"
                                        variant="outlined"
                                        validators={['required']}
                                        errorMessages={['this field is required']}

                                        fullWidth
                                        style={{margin:'5px 0px 0px 0px'}}
                                        value={this.state.formData.password}
                                    />
                                    <TextValidator
                                        label="Repeat password"
                                        onChange={this.handleChange}
                                        name="resetpassword"
                                        type="password"
                                        variant="outlined"
                                        validators={['isPasswordMatch', 'required']}
                                        errorMessages={['password mismatch', 'this field is required']}
                                        fullWidth
                                        style={{marginTop:'15px'}}
                                        value={this.state.formData.resetpassword}
                                    />
                                    <Button
                                            color="primary"
                                            variant="contained"
                                            type="submit"
                                            fullWidth
                                            disabled={this.state.disablebutton}
                                            
                                            style={{color:'white',height:'50px',marginTop:'15px',marginBottom:'30px'}}
                                        >
                                            Reset Password
                                        </Button>
                            </ValidatorForm>
                             </div>
                        </Col>
                        <Col lg="6" xs="12" style={{margin:'0px',padding:'0px'}}>
                            <img src="/static/assets/img/car/1.jpg" style={{width:'100%',height:'100%'}}></img>
                        </Col>
                        {/* <DialogComponent/> */}
                    </Row>
                </Container>
            </ThemeProvider>
        )
    }
}
const mapStateToProps = (state) =>({
    auth:state.auth
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        verifyMail:AuthAction.verifyMail,
        showDlg             :GlobalAction.showDialog

    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(ResetPasswordComponent)