import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as HowtoBuyAction from '../../redux/actions/howtobuyAction';
import * as AuthAction from '../../redux/actions/authAction';

import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

import {Col, Container, Row} from 'reactstrap'
import api from '../../services/ApiService.js'
import NormalHowtoBuyItem from './NormalHowtoBuyItem'
import HowToBuyStepBar from './HowToBuyStepBar'
import "./HowToBuy.css";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';

const myTheme = createMuiTheme({
    palette: {
      primary: {
        main: 'rgba(73, 154, 236, 1)',
      },
    },
});
class HowToBuy extends Component{
    state={
        currentIndex:0
    }
    componentDidMount(){
        //api.get('/howto')
        this.props.getHowtobuy({
            country:this.props.global.countries[this.props.global.country]
        })
    }
    onClickItem=(current)=>{
        this.setState({
            currentIndex:current
        })
    }
    render(){
        const {howtobuy,loading} = this.props.howtobuyReducer
        let isArray = Array.isArray(howtobuy)
        let staticStepTitle = ['Step1','Step2','Step3','Step4','Step5']
        return(
            <ThemeProvider theme={myTheme}>

            <div>
                <Row className="HowToBuy-Content">
                    <Col md={3}>
                        <div className="HowtoBuy-Part">
                        <div className="HowtoBuy-Menu">
                            <div className="HowToBuy-Title">
                                How to buy a car?
                            </div>
                            <div className="HowToBuy-List">
                                {loading&&(
                                    <div style={{height:'300px',alignItems:'center',textAlign:'center'}}>
                                        <CircularProgress/>
                                    </div>
                                )}
                                {!loading&&(
                                    <>
                                    {howtobuy.map((item,index)=>{
                                        let itemClass="HowToBuy-List-Item"
                                        if(this.state.currentIndex == index){
                                            itemClass = `${itemClass} current`
                                        }
                                        return(
                                            <Link onClick={()=>this.onClickItem(index)} style={{fontWeight:500,cursor:'pointer'}} to={`howtobuy_${item.title}`} spy={true} smooth={true} duration={500}>
                                                <div className={itemClass}>
                                                    
                                                    {`Step ${index + 1}: ${item.title}`}
                                                    
                                                </div>
                                        </Link>
                                        )
                                    })}
                                    </>
                                )}
                                
                            </div>
                        </div>
                        </div>
                    </Col>
                    <Col md={9}>
                        <div className="HowtoBuy-Part">
                            {loading && (
                                <>
                                {[0,1,2,3,4,5].map((item,index)=>{
                                    return(
                                        <>
                                        <div className="HowtoBuyStepbar">
                                        

                                        </div>

                                        <div className="HowtoBuyItem" style={{height:'250px',alignItems:'center',textAlign:'center'}}>
                                            <CircularProgress/>

                                        </div>
                                        </>
                                    )
                                })}
                                </>
                            )}
                            {!loading && (
                                <>
                                  {
                                    isArray && (
                                        <>
                                        {howtobuy.map((item,index)=>{
                                            return(
                                                
                                                <Element name={`howtobuy_${item.title}`} spy={true} smooth={true} duration={500}>
                                                    <HowToBuyStepBar onClickItem={this.onClickItem} current={index} howtobuy={howtobuy}/>
                                                    <NormalHowtoBuyItem item={item}/>
                                                </Element>

                                            )
                                        })}
                                        </>
                                    )
                                }
                                </>
                            )}
                      
                        </div>
                    </Col>
                </Row>
            </div>
            </ThemeProvider>
        )
    }
}
const mapStateToProps = (state) =>({
    auth            :state.auth,
    howtobuyReducer :state.howtobuy,
    global         :state.global
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
     // getAllInquiry             :InquiryAction.getAllInquiry,
     getHowtobuy:HowtoBuyAction.getHowtobuy
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(HowToBuy)

